import React, { useState, useEffect } from "react";
import ReactECharts from "echarts-for-react";

export default function LiveRateChart({ data = [] }) {
  //   const [data, setData] = useState({
  //     time: [],
  //     heartRate: [],
  //     breathRate: [],
  //   });

  //   useEffect(() => {
  //     const interval = setInterval(() => {
  //       const now = new Date();
  //       const newTime = `${now.getHours()}:${now.getMinutes()}:${now.getSeconds()}`;
  //       const newHeartRate = Math.floor(Math.random() * (100 - 60 + 1)) + 60; // Random heart rate between 60-100
  //       const newBreathRate = Math.floor(Math.random() * (30 - 12 + 1)) + 12; // Random breath rate between 12-30

  //       setData((prevData) => ({
  //         time: [...prevData.time, newTime].slice(-30), // Keep only the last 10 entries
  //         heartRate: [...prevData.heartRate, newHeartRate].slice(-30),
  //         breathRate: [...prevData.breathRate, newBreathRate].slice(-30),
  //       }));
  //     }, 1000);

  //     return () => clearInterval(interval);
  //   }, []);

  const rateOption = {
    tooltip: {
      trigger: "axis",
      axisPointer: {
        type: "line",
      },
      formatter: function (params) {
        // Customize the tooltip content
        const { name, value } = params[0];
        return `Time: ${name}<br/>Rate: ${value}`;
      },
      textStyle: {
        color: "#fff", // Text color
        fontSize: 12, // Font size
      },
      backgroundColor: "rgba(0, 0, 0, 0.7)", // Background color
      borderWidth: 0, // Remove border
    },
    grid: {
      left: "0%",
      right: "5%",
      bottom: "0%",
      top: "0%",
    },
    xAxis: {
      type: "category",
      boundaryGap: false,
      data: data?.time,
      axisLine: { show: false },
      axisTick: { show: false },
      axisLabel: { show: false },
    },
    yAxis: {
      type: "value",
      axisLine: { show: false },
      axisTick: { show: false },
      axisLabel: { show: false },
      splitLine: { show: false },
    },
    series: [
      {
        name: "Heart Rate",
        data: data?.breathRate,
        type: "line",
        smooth: false,
        lineStyle: {
          width: 2,
          color: "#FFFFFF", // Set line color to white
        },
        itemStyle: {
          opacity: 0,
        },
        areaStyle: {
          color: {
            type: "linear",
            x: 0,
            y: 0,
            x2: 0,
            y2: 1,
            colorStops: [
              { offset: 0, color: "rgba(255, 255, 255, 0.5)" }, // Top color
              { offset: 1, color: "rgba(255, 255, 255, 0)" }, // Bottom color
            ],
          },
        },
      },
    ],
  };

  return <ReactECharts option={rateOption} style={{ height: "65px" }} />;
}
